.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

/* Controls main profile info appearance */
.profile{
  font-size: 20px;
  text-align: left;
  width: 500px;
  margin-left: 100px;
}

.profileAvatar{
  max-width:40px;
  height:auto;
  display:inline-block;
}

.profileName{
  display:inline-block;
  font-size: 40px;
  font-weight: bold;
}

/* Controls keyword box appearance */
.keywords{
  background-color: #cccfcd;
  border-color: #000;
  border-width: 1px;
  border-radius: 5px;
  margin-left: 20px;
  padding: 5px 5px 5px 5px;
}

/* contains balloons */
.calendar {
  display:inline-block;
  width: 90%;
  position: relative;
  text-align: center;
  margin-bottom: 40px;
}

/* appearance of hover text */
.customTheme {
  text-align: left;
  width: 300px;
}

/* contains balloons */
.container {
  display:inline-block;
  width:170px;
  height:450px;
  position: relative;
  text-align: center;
  color: white;
  margin:10px;
  left:35%;
}

.maxwide {
  max-width: 850px;
  left:50%;
  text-align: center;
}

/* centers text onto balloon */
.centered {
  position: absolute;
  text-transform: uppercase;
  font-size: 25px;
  line-height: 25px;
  font-weight:bold;
  top: 20%;
  left: 50%;
  transform: translate(-40%, -60%);
}

